import React from 'react';
import Grid from '@mui/material/Grid';
import Pagination from '@mui/material/Pagination';
import { SENTENCES_PER_PAGE } from '../consts';
import SentenceCard from './SentenceCard';

const SentenceList = function SentenceList({
  filteredSentences, displayedSentences, page, setPage,
}) {
  return (
    <>
      <Grid container spacing={2}>
        {displayedSentences.map((sentence, index) => (
          <Grid item xs={12} key={index}>
            <SentenceCard sentence={sentence} />
          </Grid>
        ))}
      </Grid>
      <Grid container justifyContent="center" sx={{ p: 2 }}>
        <Pagination
          count={Math.ceil(filteredSentences.length / SENTENCES_PER_PAGE)}
          color="primary"
          page={page}
          onChange={(_, selectedPage) => {
            setPage(selectedPage);
          }}
        />
      </Grid>
    </>
  );
};

export default SentenceList;
