import { useEffect, useState } from 'react';
import videos from '../data/selected_data_videos.json';
import { VIDEOS_PER_PAGE, CHANNELS } from '../consts';

const years = [...new Set(videos.map((video) => video.year))].sort();

function useVideoSelector() {
  const [filteredVideos, setFilteredVideos] = useState(videos);
  const [displayedVideos, setDisplayedVideos] = useState([]);
  const [page, setPage] = useState(1);
  const [selectedChannels, setSelectedChannels] = useState(CHANNELS);
  const [selectedYears, setSelectedYears] = useState(years);

  useEffect(() => {
    function filterVideos(video) {
      return selectedChannels.includes(video.channel) && selectedYears.includes(video.year);
    }
    setFilteredVideos(videos.filter(filterVideos));
  }, [selectedChannels, selectedYears]);

  useEffect(() => {
    const pageCount = Math.ceil(filteredVideos.length / VIDEOS_PER_PAGE);
    if (page > pageCount) {
      setPage(pageCount);
    } else if (pageCount !== 0 && page === 0) {
      setPage(1);
    }
    const startIndex = VIDEOS_PER_PAGE * (page - 1);
    const endIndex = VIDEOS_PER_PAGE * page;
    setDisplayedVideos(filteredVideos.slice(startIndex, endIndex));
  }, [page, filteredVideos]);

  const toggleChannel = (channel) => {
    if (selectedChannels.includes(channel)) {
      setSelectedChannels(selectedChannels.filter((c) => c !== channel));
    } else {
      setSelectedChannels([...selectedChannels, channel]);
    }
  };

  const toggleYear = (year) => {
    if (selectedYears.includes(year)) {
      setSelectedYears(selectedYears.filter((c) => c !== year));
    } else {
      setSelectedYears([...selectedYears, year]);
    }
  };

  return {
    filteredVideos,
    setFilteredVideos,
    displayedVideos,
    setDisplayedVideos,
    page,
    setPage,
    selectedChannels,
    setSelectedChannels,
    selectedYears,
    setSelectedYears,
    toggleChannel,
    toggleYear,
    years,
  };
}

export default useVideoSelector;
