import React from 'react';
import FormControl from '@mui/material/FormControl';
import FormLabel from '@mui/material/FormLabel';
import FormGroup from '@mui/material/FormGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import Button from '@mui/material/Button';
import ButtonGroup from '@mui/material/ButtonGroup';
import { CHANNELS, CHARTS } from '../consts';

const VideoFilter = function VideoFilter({
  selectedChannels,
  setSelectedChannels,
  toggleChannel,
  years,
  selectedYears,
  setSelectedYears,
  toggleYear,
  selectedCharts,
  setSelectedCharts,
  toggleChart,
}) {
  return (
    <>
      <FormControl component="fieldset" variant="standard" sx={{ m: 2 }}>
        <FormLabel component="legend">Channel</FormLabel>
        <ButtonGroup variant="outlined" size="small" sx={{ my: 1 }}>
          <Button
            disabled={CHANNELS.every((channel) => selectedChannels.includes(channel))}
            onClick={() => {
              setSelectedChannels(CHANNELS);
            }}
          >
            Select all
          </Button>
          <Button
            disabled={CHANNELS.every((channel) => !selectedChannels.includes(channel))}
            onClick={() => {
              setSelectedChannels([]);
            }}
          >
            Clear
          </Button>
        </ButtonGroup>
        <FormGroup>
          {CHANNELS.map((channel, index) => (
            <FormControlLabel
              control={(
                <Checkbox
                  checked={selectedChannels.includes(channel)}
                  onChange={() => { toggleChannel(channel); }}
                  name={channel}
                />
            )}
              key={index}
              label={channel}
            />
          ))}
        </FormGroup>
      </FormControl>
      <FormControl component="fieldset" variant="standard" sx={{ m: 2 }}>
        <FormLabel component="legend">Year</FormLabel>
        <ButtonGroup variant="outlined" size="small" sx={{ my: 1 }}>
          <Button
            disabled={years.every((year) => selectedYears.includes(year))}
            onClick={() => {
              setSelectedYears(years);
            }}
          >
            Select all
          </Button>
          <Button
            disabled={years.every((year) => !selectedYears.includes(year))}
            onClick={() => {
              setSelectedYears([]);
            }}
          >
            Clear
          </Button>
        </ButtonGroup>
        <FormGroup row>
          {years.map((year, index) => (
            <FormControlLabel
              control={(
                <Checkbox
                  checked={selectedYears.includes(year)}
                  onChange={() => { toggleYear(year); }}
                  name={year.toString()}
                />
            )}
              key={index}
              label={year}
            />
          ))}
        </FormGroup>
      </FormControl>
      {selectedCharts !== undefined && (
      <FormControl component="fieldset" variant="standard" sx={{ m: 2 }}>
        <FormLabel component="legend">Chart</FormLabel>
        <ButtonGroup variant="outlined" size="small" sx={{ my: 1 }}>
          <Button
            disabled={CHARTS.every((chart) => selectedCharts.includes(chart))}
            onClick={() => {
              setSelectedCharts(CHARTS);
            }}
          >
            Select all
          </Button>
          <Button
            disabled={CHARTS.every((chart) => !selectedCharts.includes(chart))}
            onClick={() => {
              setSelectedCharts([]);
            }}
          >
            Clear
          </Button>
        </ButtonGroup>
        <FormGroup row>
          {CHARTS.map((chart, index) => (
            <FormControlLabel
              control={(
                <Checkbox
                  checked={selectedCharts.includes(chart)}
                  onChange={() => { toggleChart(chart); }}
                  name={chart.toString()}
                />
            )}
              key={index}
              label={chart}
            />
          ))}
        </FormGroup>
      </FormControl>
      )}
    </>
  );
};

export default VideoFilter;
