import { useState, useEffect } from 'react';
import {
  SENTENCES_PER_PAGE,
  DATA_INSIGHTS,
  DATA_CONTEXTS,
  CHARTS,
  OBJECTS,
  EFFECTS,
  NARRATION_ANIMATION_PAIR,
  INDEPENDENT_NARRATION,
  INDEPENDENT_ANIMATION,
  NARRATION_ANIMATION_MATCHINGS,
} from '../consts';
import sentences from '../data/sentence_labels.json';

function objectMatches(source, target) {
  if (target === 'axis') {
    return source === 'axis' || source === 'axes';
  }
  return source.startsWith(target);
}

function useSentenceSelector() {
  const [filteredSentences, setFilteredSentences] = useState(sentences);
  const [displayedSentences, setDisplayedSentences] = useState([]);
  const [page, setPage] = useState(1);
  const [selectedMatchings, setSelectedMatchings] = useState(NARRATION_ANIMATION_MATCHINGS);
  const [selectedTypes, setSelectedTypes] = useState([...DATA_INSIGHTS, ...DATA_CONTEXTS]);
  const [selectedCharts, setSelectedCharts] = useState(CHARTS);
  const [selectedObjects, setSelectedObjects] = useState(OBJECTS);
  const [selectedEffects, setSelectedEffects] = useState(EFFECTS);

  useEffect(() => {
    function filterAnimationHelper(animation) {
      if (!animation) return false;
      const objectSatisfied = selectedObjects.some(
        (object) => objectMatches(animation.object, object),
      );
      const effectSatisfied = selectedEffects.includes(animation.effect);
      return objectSatisfied && effectSatisfied;
    }

    function filterLabelByType(label) {
      if ((selectedMatchings.includes(NARRATION_ANIMATION_PAIR)
           || selectedMatchings.includes(INDEPENDENT_NARRATION)
      ) && selectedTypes.includes(label.type)) {
        return true;
      }
      if (selectedMatchings.includes(INDEPENDENT_ANIMATION)
          && label.interplay === INDEPENDENT_ANIMATION) {
        return true;
      }
      return false;
    }

    function filterLabelByAnimation(label) {
      if ((selectedMatchings.includes(NARRATION_ANIMATION_PAIR)
           || selectedMatchings.includes(INDEPENDENT_ANIMATION)
      ) && label.animations.some(filterAnimationHelper)) {
        return true;
      }
      if (selectedMatchings.includes(INDEPENDENT_NARRATION)
      && label.interplay === INDEPENDENT_NARRATION) {
        return true;
      }
      return false;
    }

    function filterSentences(sentence) {
      const typeSatisfied = sentence.semantics.some(filterLabelByType);
      const chartSatisfied = selectedCharts.includes(sentence.chart);
      const animationSatisfied = sentence.semantics.some(filterLabelByAnimation);
      return typeSatisfied && chartSatisfied && animationSatisfied;
    }

    setFilteredSentences(sentences.filter(filterSentences));
  }, [selectedMatchings, selectedTypes, selectedCharts, selectedObjects, selectedEffects]);

  useEffect(() => {
    const pageCount = Math.ceil(filteredSentences.length / SENTENCES_PER_PAGE);
    if (page > pageCount) {
      setPage(pageCount);
    } else if (pageCount !== 0 && page === 0) {
      setPage(1);
    }
    const startIndex = SENTENCES_PER_PAGE * (page - 1);
    const endIndex = SENTENCES_PER_PAGE * page;
    setDisplayedSentences(filteredSentences.slice(startIndex, endIndex));
  }, [page, filteredSentences]);

  const toggleMatching = (matching) => {
    if (selectedMatchings.includes(matching)) {
      setSelectedMatchings(selectedMatchings.filter((m) => m !== matching));
    } else {
      setSelectedMatchings([...selectedMatchings, matching]);
    }
  };

  const toggleType = (type) => {
    if (selectedTypes.includes(type)) {
      setSelectedTypes(selectedTypes.filter((t) => t !== type));
    } else {
      setSelectedTypes([...selectedTypes, type]);
    }
  };

  const toggleChart = (chart) => {
    if (selectedCharts.includes(chart)) {
      setSelectedCharts(selectedCharts.filter((c) => c !== chart));
    } else {
      setSelectedCharts([...selectedCharts, chart]);
    }
  };

  const toggleObject = (object) => {
    if (selectedObjects.includes(object)) {
      setSelectedObjects(selectedObjects.filter((e) => e !== object));
    } else {
      setSelectedObjects([...selectedObjects, object]);
    }
  };

  const toggleEffect = (effect) => {
    if (selectedEffects.includes(effect)) {
      setSelectedEffects(selectedEffects.filter((e) => e !== effect));
    } else {
      setSelectedEffects([...selectedEffects, effect]);
    }
  };

  return {
    filteredSentences,
    setFilteredSentences,
    displayedSentences,
    setDisplayedSentences,
    page,
    setPage,
    selectedMatchings,
    setSelectedMatchings,
    toggleMatching,
    selectedTypes,
    setSelectedTypes,
    toggleType,
    selectedCharts,
    setSelectedCharts,
    toggleChart,
    selectedObjects,
    setSelectedObjects,
    toggleObject,
    selectedEffects,
    setSelectedEffects,
    toggleEffect,
  };
}

export default useSentenceSelector;
