import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import React, { useState } from 'react';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Drawer from '@mui/material/Drawer';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import CssBaseline from '@mui/material/CssBaseline';
import Divider from '@mui/material/Divider';
import OndemandVideoIcon from '@mui/icons-material/OndemandVideo';
import TextsmsIcon from '@mui/icons-material/Textsms';
import {
  Routes, Route, useLocation, useNavigate,
} from 'react-router-dom';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import BarChartIcon from '@mui/icons-material/BarChart';
import { DRAWER_WIDTH } from './consts';
import SentenceList from './components/SentenceList';
import VideoList from './components/VideoList';
import useSentenceSelector from './hooks/useSentenceSelector';
import useVideoSelector from './hooks/useVideoSelector';
import SentenceFilter from './components/SentenceFilter';
import VideoFilter from './components/VideoFilter';
import useClipSelector from './hooks/useClipSelector';

const App = function App() {
  const location = useLocation();
  const navigate = useNavigate();
  const {
    filteredSentences,
    displayedSentences,
    page: sentencePage,
    setPage: setSentencePage,
    selectedMatchings,
    setSelectedMatchings,
    toggleMatching,
    selectedTypes,
    setSelectedTypes,
    toggleType,
    selectedCharts: selectedSentenceCharts,
    setSelectedCharts: setSelectedSentenceCharts,
    toggleChart: toggleSentenceChart,
    selectedObjects,
    setSelectedObjects,
    toggleObject,
    selectedEffects,
    setSelectedEffects,
    toggleEffect,
  } = useSentenceSelector();
  const {
    filteredVideos,
    displayedVideos,
    page: videoPage,
    setPage: setVideoPage,
    selectedChannels: selectedVideoChannels,
    setSelectedChannels: setSelectedVideoChannels,
    toggleChannel: toggleVideoChannel,
    selectedYears: selectedVideoYears,
    setSelectedYears: setSelectedVideoYears,
    toggleYear: toggleVideoYear,
    years: videoYears,
  } = useVideoSelector();
  const {
    filteredClips,
    displayedClips,
    page: clipPage,
    setPage: setClipPage,
    selectedChannels: selectedClipChannels,
    setSelectedChannels: setSelectedClipChannels,
    toggleChannel: toggleClipChannel,
    selectedYears: selectedClipYears,
    setSelectedYears: setSelectedClipYears,
    toggleYear: toggleClipYear,
    years: clipYears,
    selectedCharts: selectedClipCharts,
    setSelectedCharts: setSelectedClipCharts,
    toggleChart: toggleClipChart,
  } = useClipSelector();
  const [mobileOpen, setMobileOpen] = useState(false);
  const narrationPageShown = location.pathname === '/' || location.pathname === '/narrations-animations';
  const videoPageShown = location.pathname === '/videos';
  const clipPageShown = location.pathname === '/clips';

  const sentenceList = (
    <SentenceList
      filteredSentences={filteredSentences}
      displayedSentences={displayedSentences}
      page={sentencePage}
      setPage={setSentencePage}
    />
  );
  const videoList = (
    <VideoList
      filteredVideos={filteredVideos}
      displayedVideos={displayedVideos}
      page={videoPage}
      setPage={setVideoPage}
    />
  );
  const clipList = (
    <VideoList
      filteredVideos={filteredClips}
      displayedVideos={displayedClips}
      page={clipPage}
      setPage={setClipPage}
    />
  );

  const sentenceFilters = (
    <SentenceFilter
      selectedMatchings={selectedMatchings}
      setSelectedMatchings={setSelectedMatchings}
      toggleMatching={toggleMatching}
      selectedTypes={selectedTypes}
      setSelectedTypes={setSelectedTypes}
      toggleType={toggleType}
      selectedCharts={selectedSentenceCharts}
      setSelectedCharts={setSelectedSentenceCharts}
      toggleChart={toggleSentenceChart}
      selectedObjects={selectedObjects}
      setSelectedObjects={setSelectedObjects}
      toggleObject={toggleObject}
      selectedEffects={selectedEffects}
      setSelectedEffects={setSelectedEffects}
      toggleEffect={toggleEffect}
    />
  );
  const videoFilters = (
    <VideoFilter
      selectedChannels={selectedVideoChannels}
      setSelectedChannels={setSelectedVideoChannels}
      toggleChannel={toggleVideoChannel}
      years={videoYears}
      selectedYears={selectedVideoYears}
      setSelectedYears={setSelectedVideoYears}
      toggleYear={toggleVideoYear}
    />
  );
  const clipFilters = (
    <VideoFilter
      selectedChannels={selectedClipChannels}
      setSelectedChannels={setSelectedClipChannels}
      toggleChannel={toggleClipChannel}
      years={clipYears}
      selectedYears={selectedClipYears}
      setSelectedYears={setSelectedClipYears}
      toggleYear={toggleClipYear}
      selectedCharts={selectedClipCharts}
      setSelectedCharts={setSelectedClipCharts}
      toggleChart={toggleClipChart}
    />
  );

  const drawer = (
    <Box sx={{ overflow: 'auto' }}>
      <List>
        <ListItem
          button
          selected={narrationPageShown}
          onClick={() => { navigate('/narrations-animations'); }}
        >
          <ListItemIcon><TextsmsIcon /></ListItemIcon>
          <ListItemText primary="Narrations & Animations" />
        </ListItem>
        <ListItem
          button
          selected={clipPageShown}
          onClick={() => { navigate('/clips'); }}
        >
          <ListItemIcon><BarChartIcon /></ListItemIcon>
          <ListItemText primary="Data Clips" />
        </ListItem>
        <ListItem
          button
          selected={videoPageShown}
          onClick={() => { navigate('/videos'); }}
        >
          <ListItemIcon><OndemandVideoIcon /></ListItemIcon>
          <ListItemText primary="Data Videos" />
        </ListItem>
      </List>
      <Divider />
      <Routes>
        <Route index element={sentenceFilters} />
        <Route path="narrations-animations" element={sentenceFilters} />
        <Route path="videos" element={videoFilters} />
        <Route path="clips" element={clipFilters} />
      </Routes>
    </Box>
  );

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const container = window !== undefined ? () => window.document.body : undefined;

  return (
    <Box sx={{ display: 'flex' }}>
      <CssBaseline />
      <AppBar
        position="fixed"
        sx={{
          width: { sm: `calc(100% - ${DRAWER_WIDTH}px)` },
          ml: { sm: `${DRAWER_WIDTH}px` },
        }}
      >
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            edge="start"
            onClick={handleDrawerToggle}
            sx={{ mr: 2, display: { sm: 'none' } }}
          >
            <MenuIcon />
          </IconButton>
          <Typography variant="h6" noWrap component="div">
            Narrations and Animations in Data Videos
          </Typography>
        </Toolbar>
      </AppBar>
      <Box
        component="nav"
        sx={{ width: { sm: DRAWER_WIDTH }, flexShrink: { sm: 0 } }}
        aria-label="mailbox folders"
      >
        <Drawer
          container={container}
          variant="temporary"
          open={mobileOpen}
          onClose={handleDrawerToggle}
          ModalProps={{
            keepMounted: true,
          }}
          sx={{
            display: { xs: 'block', sm: 'none' },
            '& .MuiDrawer-paper': { boxSizing: 'border-box', width: DRAWER_WIDTH },
          }}
        >
          {drawer}
        </Drawer>
        <Drawer
          variant="permanent"
          sx={{
            display: { xs: 'none', sm: 'block' },
            '& .MuiDrawer-paper': { boxSizing: 'border-box', width: DRAWER_WIDTH },
          }}
          open
        >
          {drawer}
        </Drawer>
      </Box>
      <Box
        component="main"
        sx={{ flexGrow: 1, p: 3, width: { sm: `calc(100% - ${DRAWER_WIDTH}px)` } }}
      >
        <Toolbar />
        <Routes>
          <Route index element={sentenceList} />
          <Route path="narrations-animations" element={sentenceList} />
          <Route path="videos" element={videoList} />
          <Route path="clips" element={clipList} />
        </Routes>
      </Box>
    </Box>
  );
};

export default App;
