import { useEffect, useState } from 'react';
import clips from '../data/selected_data_clips.json';
import { VIDEOS_PER_PAGE, CHANNELS, CHARTS } from '../consts';

const years = [...new Set(clips.map((video) => video.year))].sort();

function useClipSelector() {
  const [filteredClips, setFilteredClips] = useState(clips);
  const [displayedClips, setDisplayedClips] = useState([]);
  const [page, setPage] = useState(1);
  const [selectedChannels, setSelectedChannels] = useState(CHANNELS);
  const [selectedYears, setSelectedYears] = useState(years);
  const [selectedCharts, setSelectedCharts] = useState(CHARTS);

  useEffect(() => {
    function filterClips(clip) {
      const channelSatisfied = selectedChannels.includes(clip.channel);
      const yearSatisfied = selectedYears.includes(clip.year);
      const chartSatisfied = selectedCharts.includes(clip.chart);
      return channelSatisfied && yearSatisfied && chartSatisfied;
    }
    setFilteredClips(clips.filter(filterClips));
  }, [selectedChannels, selectedYears, selectedCharts]);

  useEffect(() => {
    const pageCount = Math.ceil(filteredClips.length / VIDEOS_PER_PAGE);
    if (page > pageCount) {
      setPage(pageCount);
    } else if (pageCount !== 0 && page === 0) {
      setPage(1);
    }
    const startIndex = VIDEOS_PER_PAGE * (page - 1);
    const endIndex = VIDEOS_PER_PAGE * page;
    setDisplayedClips(filteredClips.slice(startIndex, endIndex));
  }, [page, filteredClips]);

  const toggleChannel = (channel) => {
    if (selectedChannels.includes(channel)) {
      setSelectedChannels(selectedChannels.filter((c) => c !== channel));
    } else {
      setSelectedChannels([...selectedChannels, channel]);
    }
  };

  const toggleYear = (year) => {
    if (selectedYears.includes(year)) {
      setSelectedYears(selectedYears.filter((c) => c !== year));
    } else {
      setSelectedYears([...selectedYears, year]);
    }
  };

  const toggleChart = (chart) => {
    if (selectedCharts.includes(chart)) {
      setSelectedCharts(selectedCharts.filter((c) => c !== chart));
    } else {
      setSelectedCharts([...selectedCharts, chart]);
    }
  };

  return {
    filteredClips,
    setFilteredClips,
    displayedClips,
    setDisplayedClips,
    page,
    setPage,
    selectedChannels,
    setSelectedChannels,
    selectedYears,
    setSelectedYears,
    toggleChannel,
    toggleYear,
    years,
    selectedCharts,
    setSelectedCharts,
    toggleChart,
  };
}

export default useClipSelector;
