import React from 'react';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';
import Stack from '@mui/material/Stack';
import Chip from '@mui/material/Chip';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import YouTube from 'react-youtube';
import { EMPTY_SENTENCE, EMPTY_SEMANTICS, INDEPENDENT_NARRATION } from '../consts';

const SentenceCard = function SentenceCard({ sentence }) {
  function animationList(animations) {
    return (
      <>
        <Typography mt={1}><b>Animations:</b></Typography>
        { animations.map((animation, index) => (
          <Typography key={index}>
            {animation.object}
            {' '}
            +
            {' '}
            {animation.effect}
          </Typography>
        )) }
      </>
    );
  }

  function semanticDisplay(label, index) {
    return (
      <Paper variant="outlined" key={index} sx={{ p: 1 }}>
        <Grid container alignItems="center" spacing={1}>
          <Grid item xs={4}>
            <Chip
              label={label.type !== EMPTY_SEMANTICS ? label.type : '<NO INSIGHT/CONTEXT>'}
              sx={{ maxWidth: '100%' }}
            />
          </Grid>
          <Grid item xs={8} sx={{ color: 'text.secondary' }}>
            <Typography><b>Relationship:</b></Typography>
            <Typography>{label.interplay}</Typography>
            {label.interplay !== INDEPENDENT_NARRATION && animationList(label.animations)}
          </Grid>
        </Grid>
      </Paper>
    );
  }

  return (
    <Card>
      <CardContent>
        <Grid container spacing={2}>
          <Grid item xs={12} lg={6}>
            <Typography variant="h5" component="p">
              {sentence.text !== EMPTY_SENTENCE ? sentence.text : '<NO SENTENCE>'}
            </Typography>
            <Stack spacing={1} marginTop={1} sx={{ maxHeight: '400px', overflow: 'auto' }}>
              {sentence.semantics.map(
                (label, index) => semanticDisplay(label, index),
              )}
            </Stack>
          </Grid>
          <Grid item xs={12} lg={6}>
            <YouTube
              opts={{
                width: '100%',
                playerVars: {
                  start: sentence.timestamp,
                },
              }}
              videoId={sentence.id}
            />
            <Grid container sx={{ color: 'text.secondary' }} spacing={2}>
              <Grid item xs={9}>
                <Typography>
                  {sentence.title}
                  {' '}
                  (
                  {new Date(sentence.timestamp * 1000).toISOString().substr(14, 5)}
                  )
                </Typography>
              </Grid>
              <Grid item xs={3}>
                <Typography align="right">
                  {sentence.chart[0].toUpperCase() + sentence.chart.substring(1)}
                </Typography>
              </Grid>
            </Grid>
            <Grid container sx={{ color: 'text.secondary' }} spacing={2}>
              <Grid item xs={9}>
                <Typography>
                  {sentence.channel}
                </Typography>
              </Grid>
              <Grid item xs={3}>
                <Typography align="right">
                  {sentence.year}
                </Typography>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </CardContent>
    </Card>
  );
};

export default SentenceCard;
