import React from 'react';
import FormControl from '@mui/material/FormControl';
import FormLabel from '@mui/material/FormLabel';
import FormGroup from '@mui/material/FormGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import Button from '@mui/material/Button';
import ButtonGroup from '@mui/material/ButtonGroup';
import Checkbox from '@mui/material/Checkbox';
import {
  DATA_INSIGHTS,
  DATA_CONTEXTS,
  CHARTS,
  OBJECTS,
  EFFECTS,
  NARRATION_ANIMATION_MATCHINGS,
  INDEPENDENT_ANIMATION,
  INDEPENDENT_NARRATION,
} from '../consts';

const SentenceFilter = function SentenceFilter({
  selectedMatchings,
  setSelectedMatchings,
  toggleMatching,
  selectedTypes,
  setSelectedTypes,
  toggleType,
  selectedCharts,
  setSelectedCharts,
  toggleChart,
  selectedObjects,
  setSelectedObjects,
  toggleObject,
  selectedEffects,
  setSelectedEffects,
  toggleEffect,
}) {
  const semanticTypeFiltersDisabled = selectedMatchings.length === 1
                                      && selectedMatchings[0] === INDEPENDENT_ANIMATION;
  const animationFiltersDisabled = selectedMatchings.length === 1
                                   && selectedMatchings[0] === INDEPENDENT_NARRATION;

  const matchingFilters = (
    <FormControl component="fieldset" variant="standard" sx={{ m: 2 }}>
      <FormLabel component="legend">Matching between Narrations and Animations</FormLabel>
      <ButtonGroup variant="outlined" size="small" sx={{ my: 1 }}>
        <Button
          disabled={NARRATION_ANIMATION_MATCHINGS.every(
            (matching) => selectedMatchings.includes(matching),
          )}
          onClick={() => {
            setSelectedMatchings(NARRATION_ANIMATION_MATCHINGS);
          }}
        >
          Select all
        </Button>
        <Button
          disabled={NARRATION_ANIMATION_MATCHINGS.every(
            (matching) => !selectedMatchings.includes(matching),
          )}
          onClick={() => {
            setSelectedMatchings([]);
          }}
        >
          Clear
        </Button>
      </ButtonGroup>
      <FormGroup row>
        {NARRATION_ANIMATION_MATCHINGS.map((matching, index) => (
          <FormControlLabel
            control={(
              <Checkbox
                checked={selectedMatchings.includes(matching)}
                onChange={() => { toggleMatching(matching); }}
                name={matching}
              />
            )}
            key={index}
            label={matching}
          />
        ))}
      </FormGroup>
    </FormControl>
  );

  const semanticTypeFilterConfig = [
    { description: 'Data insight', list: DATA_INSIGHTS },
    { description: 'Data context', list: DATA_CONTEXTS },
  ];
  const semanticTypeFilters = semanticTypeFilterConfig.map(({ description, list }) => (
    <FormControl component="fieldset" variant="standard" sx={{ m: 2 }}>
      <FormLabel component="legend">{description}</FormLabel>
      <ButtonGroup variant="outlined" size="small" sx={{ my: 1 }}>
        <Button
          disabled={semanticTypeFiltersDisabled
             || list.every((type) => selectedTypes.includes(type))}
          onClick={() => {
            setSelectedTypes([...new Set([...list, ...selectedTypes])]);
          }}
        >
          Select all
        </Button>
        <Button
          disabled={semanticTypeFiltersDisabled
            || list.every((type) => !selectedTypes.includes(type))}
          onClick={() => {
            setSelectedTypes(selectedTypes.filter((type) => !list.includes(type)));
          }}
        >
          Clear
        </Button>
      </ButtonGroup>
      <FormGroup>
        {list.map((type, index) => (
          <FormControlLabel
            control={(
              <Checkbox
                checked={selectedTypes.includes(type)}
                onChange={() => { toggleType(type); }}
                name={type}
                disabled={semanticTypeFiltersDisabled}
              />
              )}
            key={index}
            label={type}
          />
        ))}
      </FormGroup>
    </FormControl>
  ));

  const chartFilters = (
    <FormControl component="fieldset" variant="standard" sx={{ m: 2 }}>
      <FormLabel component="legend">Chart</FormLabel>
      <ButtonGroup variant="outlined" size="small" sx={{ my: 1 }}>
        <Button
          disabled={CHARTS.every((chart) => selectedCharts.includes(chart))}
          onClick={() => {
            setSelectedCharts(CHARTS);
          }}
        >
          Select all
        </Button>
        <Button
          disabled={CHARTS.every((chart) => !selectedCharts.includes(chart))}
          onClick={() => {
            setSelectedCharts([]);
          }}
        >
          Clear
        </Button>
      </ButtonGroup>
      <FormGroup row>
        {CHARTS.map((chart, index) => (
          <FormControlLabel
            control={(
              <Checkbox
                checked={selectedCharts.includes(chart)}
                onChange={() => { toggleChart(chart); }}
                name={chart}
              />
            )}
            key={index}
            label={chart}
          />
        ))}
      </FormGroup>
    </FormControl>
  );

  const objectFilters = (
    <FormControl component="fieldset" variant="standard" sx={{ m: 2 }}>
      <FormLabel component="legend">Animated object</FormLabel>
      <ButtonGroup variant="outlined" size="small" sx={{ my: 1 }}>
        <Button
          disabled={animationFiltersDisabled
             || OBJECTS.every((object) => selectedObjects.includes(object))}
          onClick={() => {
            setSelectedObjects(OBJECTS);
          }}
        >
          Select all
        </Button>
        <Button
          disabled={animationFiltersDisabled
            || OBJECTS.every((object) => !selectedObjects.includes(object))}
          onClick={() => {
            setSelectedObjects([]);
          }}
        >
          Clear
        </Button>
      </ButtonGroup>
      <FormGroup row>
        {OBJECTS.map((object, index) => (
          <FormControlLabel
            control={(
              <Checkbox
                checked={selectedObjects.includes(object)}
                onChange={() => { toggleObject(object); }}
                name={object}
                disabled={animationFiltersDisabled}
              />
            )}
            key={index}
            label={object}
          />
        ))}
      </FormGroup>
    </FormControl>
  );

  const effectFilters = (
    <FormControl component="fieldset" variant="standard" sx={{ m: 2 }}>
      <FormLabel component="legend">Graphical effect</FormLabel>
      <ButtonGroup variant="outlined" size="small" sx={{ my: 1 }}>
        <Button
          disabled={animationFiltersDisabled
            || EFFECTS.every((effect) => selectedEffects.includes(effect))}
          onClick={() => {
            setSelectedEffects(EFFECTS);
          }}
        >
          Select all
        </Button>
        <Button
          disabled={animationFiltersDisabled
            || EFFECTS.every((effect) => !selectedEffects.includes(effect))}
          onClick={() => {
            setSelectedEffects([]);
          }}
        >
          Clear
        </Button>
      </ButtonGroup>
      <FormGroup row>
        {EFFECTS.map((effect, index) => (
          <FormControlLabel
            control={(
              <Checkbox
                checked={selectedEffects.includes(effect)}
                onChange={() => { toggleEffect(effect); }}
                name={effect}
                disabled={animationFiltersDisabled}
              />
            )}
            key={index}
            label={effect}
          />
        ))}
      </FormGroup>
    </FormControl>
  );

  return (
    <>
      {chartFilters}
      {semanticTypeFilters}
      {objectFilters}
      {effectFilters}
      {matchingFilters}
    </>
  );
};

export default SentenceFilter;
