import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import React from 'react';
import Typography from '@mui/material/Typography';
import YouTube from 'react-youtube';
import Grid from '@mui/material/Grid';

const VideoCard = function VideoCard({ video }) {
  return (
    <Card sx={{ height: '100%' }}>
      <CardContent>
        <YouTube
          opts={video.timestamp ? {
            width: '100%',
            playerVars: {
              start: video.timestamp,
            },
          } : {
            width: '100%',
          }}
          videoId={video.id}
        />
        <Grid container spacing={2}>
          <Grid item xs={video.chart ? 9 : 12}>
            <Typography>
              {video.title}
              {video.start && ` (${video.start}â€“${video.end})`}
            </Typography>
          </Grid>
          {video.chart && (
          <Grid item xs={3}>
            <Typography align="right">
              {video.chart && (video.chart[0].toUpperCase() + video.chart.substring(1))}
            </Typography>
          </Grid>
          )}
        </Grid>
        <Grid container sx={{ color: 'text.secondary' }} spacing={2}>
          <Grid item xs={9}>
            <Typography>
              {video.channel}
            </Typography>
          </Grid>
          <Grid item xs={3}>
            <Typography align="right">
              {video.year}
            </Typography>
          </Grid>
        </Grid>
      </CardContent>
    </Card>
  );
};

export default VideoCard;
